header {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 60vh;
  background-size: 100% 200%;
  background-image: linear-gradient(to top, transparent 50%, rgba(255, 255, 255, 0.3) 100%),
    url('../../../public/assets/img/banner3.jpg');
  box-shadow: 0 0 10px 1px #363636;
}
.infoAndTitle {
  position: relative;
}
.infoContainer {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  position: relative;
  top: min(52.5vw, 21.25vh); /* (profileImg + fullName)/2 */
}
.headerTitleContainer {
  display: flex;
  justify-content: center;
  width: 60%;
}
.headerTitle {
  position: relative;
  margin-left: 8%;
  padding-top: 2vh;
  font-size: xx-large;
  font-family: Comfortaa;
  text-shadow: 0.8px 0.8px rgb(0, 0, 0);
  color: rgb(200, 200, 200);
}
.headerTitle::before {
  content: '';
  position: absolute;
  width: 117%;
  right: 5%;
  top: 0;
  border-top: 3px solid rgb(200, 200, 200);
}
.userInfo {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: relative;
  margin-right: 33px;
  z-index: 1;
}
.profileImgContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.profileImg {
  height: min(80vw, 30vh);
  margin-top: 15px;
  border-radius: 50%;
  box-shadow: 0 0 5px #08001e;
}
.fullName {
  line-height: min(15vw, 7vh);
  font-size: x-large;
  font-weight: lighter;
}
@media (max-width: 650px) {
  .headerTitle {
    margin-bottom: 15px;
    margin-left: 0;
    text-align: center;
  }
  .headerTitle::before {
    width: 78%;
    right: 18%;
  }
  .infoContainer {
    flex-direction: column;
    align-items: center;
  }
  .userInfo {
    margin: 0;
  }
}
