.skillsSection {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 100%;
}
.titleContainer {
  display: flex;
  align-items: center;
}
.title {
  margin-bottom: 20px;
  font-size: x-large;
}
.softAndHard,
.languages {
  display: flex;
  flex-direction: column;

  align-items: center;
}
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.addButton {
  align-self: center;
  margin: 20px 0;
}
