.techsAndInfoSection {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 100%;
}
.personalInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 50%;
  margin: 0 0 20vh 3vw;
}
.personalInfoTitle {
  margin-bottom: 20px;
  padding-top: 1vh;
  font-size: x-large;
  font-weight: 500;
}
.personalInfoTitle::before {
  content: '';
  position: absolute;
  width: 60%;
  left: 0;
  top: 0;
  border-top: 2px solid #2a2a2a;
}
.aboutMe {
  width: 90%;
  text-align: justify;
}

.aboutMeInput {
  height: 80px;
  width: 100%;
  padding: 2px;
  font-size: larger;
  resize: vertical;
}

@media (max-width: 650px) {
  .personalInfo {
    width: 90%;
  }
  .personalInfoTitle::before {
    width: 82%;
  }
}
