.button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 5px 10px;
  font-size: min(7vw, 2.6vh);
  transition: transform 0.3s ease;
  transform: scale(1);
  box-shadow: 0.5px 0.5px 1px #131313;
}
.buttonPressed {
  transform: scale(0.95);
}
